






































import { defineComponent, ref } from "@vue/composition-api"
import CaldatesPopup from "@c/organisms/caldate/CaldatesPopup.vue"

export default defineComponent({

  name : "CurrentCaldates",

  components:{
    CaldatesPopup
  },

  props:{

    caldates:{
      type:Array,
      default:[]
    },

    more:{
      type:Boolean,
      default:false
    },

    title:String
  },

  setup() {

    var morePopup = ref(false)

    return{
      morePopup
    }

  }
});
